// function 'this' scope gets bound to the handsontable class instance
export function beforeChange(changes, source) {
  if (source === 'loadData') {
    return;
  }

  const columns = this.config.HandsontableConfig.columns;

  changes.forEach(change => {
    // each change is one cell array with: [rowIndex, colIndex, oldValue, newValue]
    if (columns[change[1]].type === 'dropdown' && columns[change[1]].source.indexOf(change[3]) === -1) {

      // Set value to empty of not present in a column list
      change[3] = '';
    }

  });
}

export function beforeValidate(value, row, col) {
  const columns = this.config.HandsontableConfig.columns;

  // each change is one cell array with: [rowIndex, colIndex, oldValue, newValue]
  if (columns[col].type === 'dropdown' && columns[col].source.indexOf(value) === -1) {
    return '';
  }

  return value;
}
