import debounce from 'lodash/debounce';
import moduleconfig from "../u-moduleconfig/moduleconfig";
import validators from './c-spreadsheet.validators';
import { beforeChange, beforeValidate } from './c-spreadsheet.changeHandlers';
import Handsontable from 'handsontable';
import './o-handsontable.full.scss';

export default class Spreadsheet {

  constructor(element, options) {

    // Parse and/or arrange any given config parameters
    this.config = moduleconfig({
      "options": options,
      "element": element,
      "defaults": {}
    });
    this.element = element;
    this.init();
  }

  // Initialise module instance
  init() {

    // Make sure we have a HandsontableConfig
    this.config.HandsontableConfig = this.config.HandsontableConfig || {};

    // Use dummy data for testing
    if (this.config.DummyData && Object.prototype.toString.call(this.config.DummyData) === '[object Array]') {
      this.config.HandsontableConfig.data = Handsontable.helper.createSpreadsheetData(this.config.DummyData[0], this.config.DummyData[1]);
    }

    // If we have a BlueriqData object, process its data, and set additional Blueriq implementation defaults.
    if (this.config.BlueriqData) {
      // register custom renderer specific for dropdown columns (this column does not use the general renderer)
      Handsontable.renderers.registerRenderer('bqdropdown', Spreadsheet.BqDropdownRenderer(this.config.BlueriqData, Handsontable, this));

      this.config.HandsontableConfig.data = Spreadsheet.BqGetInstanceValues(this.config.BlueriqData);
      this.config.HandsontableConfig.rowHeaders = true;
      this.config.HandsontableConfig.colHeaders = this.config.BlueriqData.headers;
      this.config.HandsontableConfig.columns = Spreadsheet.BqGetColumns(this.config.BlueriqData);
      this.config.HandsontableConfig.renderer = Spreadsheet.BqRenderer(this.config.BlueriqData, Handsontable, this);
      this.config.HandsontableConfig.outsideClickDeselects = false;
      this.config.HandsontableConfig.contextMenu = false;
      this.config.HandsontableConfig.beforeChange = beforeChange.bind(this);
      this.config.HandsontableConfig.beforeValidate = beforeValidate.bind(this);
    }

    // Stretch narrow sheets across the full container width
    this.config.HandsontableConfig.stretchH = 'all';

    // Define dimensions for our container, so we can handle overflow for larger tables
    const rowHeight = 25,
      scrollHeight = 13,
      rows = this.config.HandsontableConfig.data.length + (this.config.HandsontableConfig.colHeaders ? 1 : 0),
      tableHeight = (rows * rowHeight) + scrollHeight;

    this.element.style.overflow = 'hidden';
    this.element.style.width = this.element.offsetWidth + 'px';
    this.element.style.height = Math.min(tableHeight, 400) + 'px';

    window.addEventListener('resize', debounce(this.onResize.bind(this), 200));

    // Instantiate our spreadsheet
    this.table = new Handsontable(this.element, this.config.HandsontableConfig);

    // shrink height if there is no scrollbar on bottom
    if (this.table.countVisibleCols() === this.table.countCols()) {
      this.element.style.height = Math.min(tableHeight - scrollHeight, 400) + 'px';
    }

    this.table.selectCell(0, 0);
    $('body').on('NN:Blueriq:completeLoading', () => {
      if (this.table && !this.element.parentNode) {
        this.table.destroy();
        this.table = null;
      }
    });

  }

  onResize() {
    this.element.style.width = 'auto'; // let the container adjust to the available space
    setTimeout(debounce(this.fixContainerWidth.bind(this), 100), 50); // wait to fix the width, to give the render a chance
  }

  fixContainerWidth() {
    this.element.style.width = this.element.offsetWidth + 'px';
  }

  static BqGetInstanceValues(bqConfig) {
    const table = [];
    const instances = bqConfig.instances;

    for (let key in instances) {
      if (!instances.hasOwnProperty(key)) {
        continue;
      }
      const instance = instances[key];

      const row = [];
      for (let i = 0; i < instance.length; i++) {
        row.push(instance[i].value);
      }
      table.push(row);
    }
    return table;
  }

  static BqGetColumns(bqConfig) {
    const instances = bqConfig.instances;
    const columns = [];

    if (instances.length) {
      for (let colIndex = 0; colIndex < instances[0].length; colIndex++) {
        if (!instances[0][colIndex].selector) {
          if (bqConfig.columns) {
            let validator = bqConfig.columns[colIndex].validator;
            if (validator) {
              bqConfig.columns[colIndex].validator = this.getValidatorFn(validator)
            }

            if (bqConfig.columns[colIndex].type === 'dropdown') {
              // use the specific custom renderer for dropdowns
              bqConfig.columns[colIndex].renderer = 'bqdropdown'
            }

            columns.push(bqConfig.columns[colIndex]);
          } else {
            columns.push({data: colIndex});
          }
        }
      }
    }
    return columns;
  }

  static getValidatorFn(validator) {

    if(validator.indexOf('/') === 0) {
      return new RegExp(validator.substring(1, validator.lastIndexOf('/')))
    }

    return validators[validator];
  }

  static renderCell(row, instances, col, cellProperties, value, td) {
    if (row < instances.length) {
      const instance = instances[row][col];

      if (cellProperties.valid === false || (instance.value === value && instance.message)) {
        if (td.parentNode) {
          td.parentNode.classList.add('htInvalid');
        }

        td.classList.add('htInvalid');
        td.title = instance.message;
      }
    }
    return td;
  }

  static BqRenderer(bqConfig, Handsontable, spreadsheet) {
    const instances = bqConfig.instances;

    return function (instance, td, row, col, prop, value, cellProperties) {
      Handsontable.renderers.TextRenderer.apply(spreadsheet, arguments);

      return Spreadsheet.renderCell(row, instances, col, cellProperties, value, td);
    };
  }

  static BqDropdownRenderer(bqConfig, Handsontable, spreadsheet) {
    const instances = bqConfig.instances;

    return function (instance, td, row, col, prop, value, cellProperties) {
      Handsontable.renderers.DropdownRenderer.apply(spreadsheet, arguments);

      return Spreadsheet.renderCell(row, instances, col, cellProperties, value, td);
    };
  }

  // Offer out Handsontable instance
  getTable() {
    return this.table;
  }
}
